.btn-primary { width: 230px; height: 60px; font-size: 20px; font-weight: 600; color: #ffffff; text-transform: uppercase; white-space: nowrap; padding-right: 120px; position: relative; margin-bottom: 80px; }

.btn-primary:before { content: ""; font-family: "iconfont"; position: absolute; right: 30px; }

.btn-primary:hover { padding-right: 0; }

.btn-primary:hover:before { opacity: 0; }

form { width: 400px; }

@media only screen and (max-width: 640px) { form { width: 100%; } }

form input, form textarea { background-color: transparent; border: solid 1px #777777; width: 100%; height: 55px; font-size: 18px; font-weight: 300; color: #222222; margin-bottom: 30px; padding-left: 20px; }

form input::placeholder, form textarea::placeholder { color: #222222; }

form textarea { resize: none; height: 220px; padding-top: 20px; }

form input[type=submit] { border-radius: 2px; background-image: linear-gradient(to right, #340d5c, #e02560); color: #ffffff; width: 160px; padding-left: 0; text-align: center; font-family: Roboto; font-size: 18.5px; font-weight: 500; text-transform: uppercase; border: none; float: left; }

.header { position: absolute; width: 100%; top: 0; padding-top: 20px; }

.header__logo { width: 210px; height: 65px; }

.header__logo img { width: 100%; height: 100%; }

.header a { position: relative; left: -330px; }

@media only screen and (max-width: 640px) { .header a { left: 0; } }

.footer { background-image: url("../img/bg-footer.jpg"); background-size: cover; padding-top: 70px; padding-bottom: 30px; color: #ffffff; }

.footer__info h2 { text-transform: uppercase; font-size: 20px; font-weight: 600; margin-bottom: 30px; }

@media only screen and (max-width: 640px) { .footer__info h2 { font-size: 30px; } }

.footer__info p { font-size: 16px; line-height: 1.73; }

@media only screen and (max-width: 640px) { .footer__info p { font-size: 24px; line-height: 1.3; margin-bottom: 20px; } }

.footer__info a { color: #ffffff; text-decoration: none; font-size: 16px; margin-bottom: 25px; display: inline-block; }

@media only screen and (max-width: 640px) { .footer__info a { font-size: 24px; } }

@media only screen and (max-width: 640px) { .footer__social { display: flex; flex-wrap: wrap; justify-content: center; } }

.footer__social__block { display: inline-block; padding: 15px 10px; border-radius: 3px; background-image: linear-gradient(to right, #340d5c, #e02560); font-size: 9px; font-weight: 900; line-height: 0.8; text-align: left; color: #fdfdfd; text-transform: uppercase; white-space: nowrap; margin-bottom: 30px; }

@media only screen and (max-width: 640px) { .footer__social__block { font-size: 20px; font-weight: 600; width: 100%; text-align: center; } }

.footer__social a { color: #666666; text-decoration: none; display: inline-block; margin-right: 10px; }

@media only screen and (max-width: 640px) { .footer__social a { font-size: 50px; } }

.footer__form { display: flex; flex-direction: column; align-items: center; margin-top: 70px; margin-bottom: 110px; }

@media only screen and (max-width: 640px) { .footer__form { margin-bottom: 40px; } }

.footer__form h2 { font-family: 'Roboto'; font-size: 34px; text-align: center; color: #ffffff; font-weight: normal; margin-bottom: 40px; }

.footer__form form { position: relative; width: 450px; }

@media only screen and (max-width: 640px) { .footer__form form { width: 100%; display: flex; flex-wrap: wrap; justify-content: center; } }

.footer__form form input { width: 100%; height: 45px; border-radius: 23px; font-size: 13px; text-align: left; color: #ffffff; margin-bottom: 20px; }

.footer__form form input::placeholder { color: rgba(255, 255, 255, 0.7); }

@media only screen and (max-width: 640px) { .footer__form form input { font-size: 24px; height: 65px; } }

.footer__form form input[type="submit"] { width: 108px; height: 39px; border-radius: 20px; background-image: linear-gradient(to right, #340d5c, #e02560); font-size: 13px; border: none; position: absolute; top: 3px; right: 3px; text-align: center; }

@media only screen and (max-width: 640px) { .footer__form form input[type="submit"] { position: static; text-align: center; height: 65px; font-size: 24px; width: 250px; } }

.footer__form a { text-decoration: none; font-size: 11.5px; color: #8c8f94; }

@media only screen and (max-width: 640px) { .footer__form a { font-size: 24px; } }

.footer hr { opacity: 0.2; border-radius: 0.4px; background-color: #fefefe; margin-bottom: 10px; }

@media only screen and (max-width: 640px) { .footer__contact { display: flex; justify-content: center; flex-wrap: wrap; } }

.footer__contact p { font-family: Roboto; font-size: 12px; font-weight: bold; color: #666666; margin-bottom: 5px; }

@media only screen and (max-width: 640px) { .footer__contact p { font-size: 24px; } }

.footer__contact a { font-family: Roboto; font-size: 13px; letter-spacing: 0.3px; color: #666666; text-decoration: none; }

@media only screen and (max-width: 640px) { .footer__contact a { font-size: 24px; } }

.footer__contact a i { font-size: 16px; position: relative; top: 4px; display: inline-block; margin-right: 5px; }

@media only screen and (max-width: 640px) { .footer__contact a i { font-size: 24px; } }

.footer__made { display: flex; justify-content: flex-end; align-items: center; height: 50px; }

@media only screen and (max-width: 640px) { .footer__made { justify-content: center; margin-top: 30px; } }

.footer__made a { text-decoration: none; letter-spacing: 0.7px; text-align: left; color: #666666; font-size: 10px; text-transform: uppercase; }

@media only screen and (max-width: 640px) { .footer__made a { font-size: 24px; } }

.intro { background: url("../img/bg-intro.jpg") no-repeat; background-size: cover; color: #ffffff; padding-top: 150px; padding-bottom: 20px; border-bottom: 1px solid #ffffff; }

.intro__abonement { display: flex; margin-bottom: 60px; }

.intro__abonement__lost { position: relative; padding-right: 35px; margin-right: 50px; }

.intro__abonement__lost:before { content: ''; display: block; width: 1px; height: 185px; background-image: linear-gradient(to bottom, #000004, #ffffff 51%, #000004 99%); position: absolute; right: 0; top: 30px; }

.intro__abonement span { font-size: 130px; font-weight: bold; }

@media only screen and (max-width: 640px) { .intro__abonement span { font-size: 90px; } }

.intro__abonement p { font-size: 32px; font-family: 'Proxima Nova'; font-weight: 800; text-transform: uppercase; margin-top: -10px; }

@media only screen and (max-width: 640px) { .intro__abonement p { font-size: 24px; } }

.intro__info { display: flex; flex-wrap: wrap; justify-content: space-between; }

@media only screen and (max-width: 640px) { .intro__info { flex-direction: column; } }

.intro__info__line { position: relative; width: 100%; margin-bottom: 30px; }

.intro__info__line hr { opacity: 0.15; border: 1px solid #ffffff; }

.intro__info__line div { width: 30px; height: 6px; background: #cc3366; position: absolute; display: block; left: 40px; top: 8px; z-index: 10; }

.intro__info__block { width: 29%; }

@media only screen and (max-width: 640px) { .intro__info__block { width: 100%; margin-bottom: 25px; } }

.intro__info__block h2 { font-size: 18px; letter-spacing: 0.9px; margin-bottom: 25px; text-transform: uppercase; opacity: 0.5; transition: all 0.3s; }

@media only screen and (max-width: 640px) { .intro__info__block h2 { margin-bottom: 10px; font-size: 24px; opacity: 1; } }

.intro__info__block p { font-size: 16px; font-family: 'Roboto'; opacity: 0.5; transition: all 0.3s; }

@media only screen and (max-width: 640px) { .intro__info__block p { font-size: 20px; opacity: 1; } }

.intro__info__block.is-active p, .intro__info__block.is-active h2 { opacity: 1; }

@media only screen and (max-width: 640px) { .intro a { margin-left: 100px; } }

.info { background-image: url("../img/bg-info.jpg"); background-size: cover; color: #ffffff; padding-top: 110px; padding-bottom: 50px; }

.info h2 { font-size: 28.5px; font-weight: bold; margin-bottom: 30px; }

@media only screen and (max-width: 640px) { .info h2 { font-size: 36px; } }

.info__place { background-image: url("../img/place.png"); position: relative; left: 50%; transform: translateX(-50%); margin-bottom: 75px; width: 570px; height: 110px; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

@media only screen and (max-width: 640px) { .info__place { width: 100%; height: 80px; } }

.info__row { display: flex; flex-wrap: wrap; justify-content: space-between; left: 50%; transform: translateX(-50%); position: relative; width: 80%; }

@media only screen and (max-width: 640px) { .info__row { flex-direction: column; } }

.info__block { display: flex; align-items: center; }

@media only screen and (max-width: 640px) { .info__block { margin-bottom: 25px; } }

.info__block__icon { margin-right: 20px; width: 45px; height: 45px; }

@media only screen and (max-width: 640px) { .info__block__icon { width: 65px; height: 65px; } }

.info__block__icon img { width: 100%; height: 100%; }

@media only screen and (max-width: 640px) { .info__block p { font-size: 24px; } }

.advantages__photo, .advantages__1, .advantages__2, .advantages__3 { height: 100%; width: 200%; position: relative; padding-top: 300px; cursor: pointer; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

@media only screen and (max-width: 640px) { .advantages__photo, .advantages__1, .advantages__2, .advantages__3 { padding: 90px 0; } }

.advantages__photo:before, .advantages__1:before, .advantages__2:before, .advantages__3:before { content: ''; opacity: 0; background-image: linear-gradient(to right, #340d5c, #e02560); position: absolute; z-index: 10; top: 0; left: 0; width: 100%; height: 100%; }

.advantages { display: flex; flex-wrap: wrap; height: 740px; overflow: hidden; position: relative; }

@media only screen and (max-width: 640px) { .advantages { height: 100%; } }

.advantages__block { background-size: cover; background-repeat: no-repeat; width: 33%; cursor: pointer; }

@media only screen and (max-width: 640px) { .advantages__block { width: 100%; } }

.advantages__block:nth-of-type(2) { transform: skew(-15deg); overflow: hidden; z-index: 11; }

@media only screen and (max-width: 640px) { .advantages__block:nth-of-type(2) { transform: skew(0); } }

.advantages__block ul { font-size: 28px; letter-spacing: 0.6px; text-align: left; color: #ffffff; z-index: 10; position: relative; opacity: 0.33; list-style: disc; transition: all 0.3s; }

.advantages__1 { background-image: url("../img/advantages/advantages-1.jpg"); left: 0px; background-position: 0px; width: 150%; }

.advantages__1 ul { left: 90px; }

.advantages__2 { background-image: url("../img/advantages/advantages-2.jpg"); transform: skew(15deg); overflow: hidden; z-index: 10; left: -120px; }

@media only screen and (max-width: 640px) { .advantages__2 { transform: skew(0); } }

.advantages__2 ul { left: 200px; }

.advantages__3 { background-image: url("../img/advantages/advantages-3.jpg"); left: -100px; width: 160%; }

.advantages__3 ul { left: 200px; }

.advantages .is-active:before { opacity: 0.33; transition: all 0.3s; }

.advantages .is-active ul { opacity: 1; }

.advantages__line { position: absolute; bottom: 120px; left: 45%; transform: translateX(-50%); width: 930px; z-index: 100; }

@media only screen and (max-width: 640px) { .advantages__line { display: none; } }

.advantages__line hr { opacity: 0.15; }

.advantages__line div { width: 30px; height: 6px; background: #cc3366; position: absolute; display: block; transform: translateX(340px); top: 3px; z-index: 10; }

.services { display: flex; color: #ffffff; flex-wrap: wrap; }

.services__block { width: 50%; height: 530px; padding: 0 70px; padding-top: 140px; position: relative; cursor: pointer; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

@media only screen and (max-width: 640px) { .services__block { width: 100%; height: 100%; padding: 100px 70px; } }

.services__block h2 { font-size: 35.5px; font-weight: bold; letter-spacing: 0.4px; color: #cc3366; margin-bottom: 30px; position: relative; z-index: 100; }

.services__block p { font-family: 'Roboto'; font-size: 16px; line-height: 1.5; letter-spacing: 0.1px; text-align: left; color: rgba(255, 255, 255, 0.3); margin-bottom: 20px; position: relative; z-index: 100; transition: all 0.3s; }

@media only screen and (max-width: 640px) { .services__block p { font-size: 20px; } }

.services__block__hover { position: absolute; z-index: 10; width: 100%; height: 100%; top: 0; left: 0; opacity: 0; background-image: linear-gradient(to right, #340d5c, #e02560); transition: all 0.3s; }

.services__block:hover p { color: #ffffff; }

.services__block:hover .services__block__hover { opacity: 0.33; }

.call { background-image: url("../img/bg-call.jpg"); padding-top: 100px; padding-bottom: 50px; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

.call .container { background-image: url("../img/circle.png"); background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

.call h2 { font-size: 30px; font-weight: bold; text-align: right; color: #ffffff; margin-bottom: 50px; }

.call a { float: right; }

.form { background-image: url("../img/bg-form.jpg"); background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; padding: 70px 0; }

.form h2 { font-family: 'Roboto'; font-size: 40px; font-weight: bold; text-align: center; color: #cc3366; margin-bottom: 80px; }

@media only screen and (max-width: 640px) { .form h2 { margin-bottom: 40px; } }

.form h3, .form a { font-family: 'Roboto'; font-size: 22px; line-height: 1.36; letter-spacing: 0.4px; text-align: left; color: #222222; font-weight: bold; text-decoration: none; }

@media only screen and (max-width: 640px) { .form h3, .form a { font-size: 30px; } }

.form h3 { margin-bottom: 30px; }

.form a { font-weight: normal; display: block; }

.form p { font-family: 'Roboto'; font-size: 18px; line-height: 1.33; text-align: left; color: #4b4b4b; margin-bottom: 123px; margin-top: -5px; }

@media only screen and (max-width: 640px) { .form p { font-size: 30px; margin-bottom: 40px; } }

.form__info { margin-left: 70px; }

@media only screen and (max-width: 640px) { .form__info { margin-left: 0; } }
